export  function base64ToFile(src, resize, name){
  const canvas = document.createElement("canvas")
  const context = canvas.getContext('2d')
  const image = new Image()
  // „Åì„Çå„ÅßMIME„Çø„Ç§„Éó„ÅåÂèñ„Çå„Å¶„Çã„Å®ÊÄù„ÅÜ„Åë„Å©Ëã•Âπ≤‰∏çÂÆâ...
  const type = src.substring(0,src.indexOf(";")).replace('data:','')
  image.src = src
  return new Promise((resolve, reject) => {
    image.onload = (() =>{  
      // „É™„Çµ„Ç§„Ç∫Âæå„ÅÆ„Çµ„Ç§„Ç∫„ÇíË®àÁÆó„Åô„Çã  
      let ratio,width,height;
      if(resize && resize > 0){
        if ( image.width > image.height ){
          ratio = image.height / image.width
          width = resize
          height = resize * ratio
        } else {
          ratio = image.width / image.height
          width = resize * ratio
          height = resize
        }
      } else {
        width = image.width
        height = image.height
      }
      
      // canvas„Çí„É™„Çµ„Ç§„Ç∫Âæå„ÅÆ„Çµ„Ç§„Ç∫„Åß‰Ωú„Çã
      canvas.height = height
      canvas.width = width
      context.drawImage(image, 0, 0, width, height)
  
      // canvas„Åã„Çâbase64ÁîªÂÉè„Éá„Éº„Çø„ÇíÂèñÂæó
      const base64 = canvas.toDataURL(type)
      // base64„Åã„ÇâBlob„Éá„Éº„Çø„Çí‰ΩúÊàê
      let barr, bin, i, len
      bin = atob(base64.split('base64,')[1])
      len = bin.length;
      barr = new Uint8Array(len)
  
      i = 0
      while ( i < len ) {
        barr[i] = bin.charCodeAt(i)
        i++
      } 
      
      // ÂºïÊï∞„Å´name„ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Å™„ÅÑÂ†¥Âêà
      if (!name) name = `${Date.now()}.${type.replace('image/','')}`
      // file„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Çí‰ΩúÊàê
      const file = new File( [barr], name , {type:type})
      resolve(file)
    })
    image.onerror = (e) => reject(e)
  })
}

export function pathToFile(src, resize){

  const canvas = document.createElement("canvas")
  const context = canvas.getContext('2d')
  const image = new Image()
  

  const name_ext = src.match(".+/(.+?)([\?#;].*)?$")[1];
  let ext = name_ext.substring(name_ext.lastIndexOf('.') + 1,name_ext.length);
  if(ext == 'jpg')ext = 'jpeg' // „Å™„Çì„Åã image/jpg„ÄÄ„Åßbase64Âåñ„Åô„Çã„Å® image/png„ÄÄ„Å´„Å™„Å£„Å¶„Åó„Åæ„ÅÜ„ÄÇ
  const type = `images/${ext}`
  image.src = src

  return new Promise((resolve, reject) => {
    image.onload = (() =>{  
      let ratio,width,height;
      if(resize && resize > 0){
        if ( image.width > image.height ){
          ratio = image.height / image.width
          width = resize
          height = resize * ratio
        } else {
          ratio = image.width / image.height
          width = resize * ratio
          height = resize
        }
      } else {
        width = image.width
        height = image.height
      }
      canvas.height = height
      canvas.width = width
      context.drawImage(image, 0, 0, width, height)
      
      const base64 = canvas.toDataURL(type);
      // base64„Åã„ÇâBlob„Éá„Éº„Çø„Çí‰ΩúÊàê
      let barr, bin, i, len
      bin = atob(base64.split('base64,')[1])
      len = bin.length;
      barr = new Uint8Array(len)
  
      i = 0
      while ( i < len ) {
        barr[i] = bin.charCodeAt(i)
        i++
      } 
      
      // file„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Çí‰ΩúÊàê
      const file = new File( [barr], name_ext , {type:type})
      resolve(file)


    })
    image.onerror = (e) => reject(e)
  })
}